.header {
    display: flex;
    justify-content: space-between;
    padding: 40px 30px;
    margin: 0;
}

.icons {
    position: relative;
}

.headerIcon {
    border-radius: 50%;
    background-color: #FFDB00;
}

.airplaneIcon {
    position: absolute;
    width: 50px;
    height: 50px;
    top: -17px;
    left: 138px;
}


